import React from 'react'

import "../css/Footer.css";
import { Link } from 'react-router-dom'
import {SidebarData} from './SidebarData'
function Footer() {
    return (
        <div className="main-footer">
            <div class="container">
                <div class="row">
                    <div class="col-sm">
                        <div class="col-sm">
                            {
                                SidebarData.map((item, index) => {
                                    return (
                                        <li className="list-unstyled">
                                                <Link to={item.path}>
                                                    {item.icon}
                                                    <span>{item.title}</span>
                                                </Link>
                                        </li>
                                    )
                                })
                            }
                        </div>
                    </div>
                    <div class="col-sm">
                        <h3 className="list-unstyled">
                            <li>México</li>
                            <li>Guadalajara</li>
                            <li>123 Streeet South North</li>
                        </h3>
                    </div>
                    <div class="col-sm">
                        <div class="col-md-3 offset-md-3">
                            <Link to={'/'}>
                                <img src='https://cdn.discordapp.com/attachments/727975122949898328/803649904773693500/No_BG.png' alt='logo' width="180" height="180" />
                            </Link>
                        </div>
                    </div>
                </div>
                <hr color="white"/>
                <div className="row">
                    <p className="col-sm">
                        <p style={{color: "white"}}>
                        &copy;{new Date().getFullYear()} - 
                            <Link to={'/games'}>
                                CONQUEROR GAMES
                            </Link>
                                |All rights reserved|
                            <Link to={'/games'}>
                                Privacy
                            </Link>  
                        </p>
                    </p>
                </div>
                <div>

                </div>
            </div>
        </div>
    );
}

export default Footer;